import React, {useRef} from 'react'
import './index.css'
import ProfileBG from './pix.png'


const Profile = () => {
    const chooseBGImageRef = useRef(null)
    const BGRef = useRef(null)

    const changeBG = () => {
        chooseBGImageRef.current.click()
    }

    const updateBackgroundImage =  (e) => {
       const file = e.target.files[0]
       const reader = new FileReader()
       reader.readAsDataURL(file)
       reader.onload = (event) => {
             BGRef.current.src = event.target.result
       }
    }
    const chooseImageRef = useRef(null)
    const Ref = useRef(null)
    const changeImage = () => {
        chooseImageRef.current.click()
    }
    const selectImage = (e) =>{
       const file = e.target.files[0]
       const reader = new FileReader()
       reader.readAsDataURL(file)
       reader.onload = (event) => {
        Ref.current.src=event.target.result
       }
    }
    return (
        <div className="main_container">
            <div className="wrapper">
                <div className="header_container">
                    <div className="background_image_box">
                        <img className="img" ref={BGRef} src={ProfileBG} alt="bd" />
                    </div> 
                    <div className="change_background_color_container">
                        <div className="change_background_color" onClick={() => changeBG()}>
                            change
                            <input ref={chooseBGImageRef} type="file" className="chooseBGImage" onChange={updateBackgroundImage} />
                        </div>
                    </div>
                    <div className="profile_picture_container">
                        <div className="profile_picture">
                             <img className="img_two" ref={Ref}  src={ProfileBG} alt="bd" />
                        </div>                      
                        <div className="change_profile_pic_container">
                                <div className="change_profile_pic" onClick={()=>changeImage()}>
                                    <input ref={chooseImageRef} type="file" className="chooseBGImage" onChange={selectImage} />
                                </div>
                              </div>
                        <div className="username">Kehinde Ayodele</div>
                        <div className="username_short_bio">I'm a software Developer</div>
                    </div>
                  
                    <div className="action_button_container">
                        <div className="action_button_wrapper">
                            <div className="action_button">Follow</div>
                            <div className="action_button">Message</div>
                        </div>
                    </div>   
                </div>
                <div  className="feeds_container">
                    <div className="pics">
                    </div>
                    <div className="feeds_name">
                    <div className="name">Ayodele Kehinde</div>
                    <div className="munite">15munite ago</div>
                    </div>   
                </div>
                <div className="feeds"> happy birthday to you Ayodele Kehinde we wish you many happy return long life and prosperity hip hip hip hip hip hip hip hip hip hip hip hip hip hip hip hurrey 
                </div>
            </div>
           
        </div>
    )
}


export default Profile